import { createTheme } from '@mui/material/styles';

const noiseTexture = `url('data:image/png;base64,')`;

const getRandomColor = () => {
    const hue = Math.floor(Math.random() * 360);
    return `hsl(${hue}, 30%, 15%)`;
};

export const createDarkGrainyTheme = () => {
    const color1 = getRandomColor();
    const color2 = getRandomColor();

    return createTheme({
        palette: {
            mode: 'dark',
            background: {
                default: color1,
            },
        },
        components: {
            MuiCssBaseline: {
                styleOverrides: {
                    body: {
                        backgroundImage: `
                            radial-gradient(ellipse at top left, ${color1}, transparent),
                            radial-gradient(ellipse at bottom right, ${color2}, transparent),
                            ${noiseTexture}
                        `,
                        backgroundBlendMode: 'overlay, overlay, normal',
                        backgroundAttachment: 'fixed',
                    },
                },
            },
        },
    });
};